#root > * {
  margin-bottom: 0.5rem;
  font-family: Arial, Helvetica, sans-serif;
}

#root > *:last-child {
  margin-bottom: 0;
}

body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #222;
  /* background: linear-gradient(45deg, rgba(255, 0, 198, 1) 0%, rgba(255, 222, 0, 1) 100%); */
  overflow: hidden;
}
